	methods: {
		onMenu(item) {
			console.log(this.$store.state);
			if (this.$util.checkLogin()) {
				if (item.key == 6) {
					console.log(item.key, '444')
					uni.showModal({
						title: '提示',
						content: '你要退出登录么?',
						success: (res) => {
							if (res.confirm) {
								uni.removeStorageSync('CODE')
								this.$store.state.userInfo.msg = ''
								uni.navigateTo({
									url: '/pages/loginBase/wx_login/wx_login'
								})
							}
						}
					})

				} else {
					uni.navigateTo({
						url: item.url
					})
				}
			}
		},
		switchPage() {
			if (this.$util.checkLogin()) {
				uni.navigateTo({
					url: '/pages/orderList/orderList?pages=user'
				})
			}
		},
		onShowBalance() {
			uni.navigateTo({
				url: '/pages/withdrawMoney/cash'
			})
		},
		longin() {
			uni.showModal({
				title: '提示',
				content: '你要退出登录么?',
				success: (res) => {
					if (res.confirm) {
						uni.removeStorageSync('CODE')
						this.$store.state.userInfo.msg = ''
						uni.navigateTo({
							url: '/pages/loginBase/wx_login/wx_login'
						})
					}
				}
			})
		},
